
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'
import Toast from 'vux/src/components/toast/index.vue'

import spreadService from "../../service/spreadService";
import createService from "../../service/createService";
import { setTimeout } from "timers";
import Swiper from "swiper";
import FlowDetail from "./../components/FlowDetail";
import TaskConfirm from "./../components/TaskConfirm";
import detail from "./../mixins/detail";
export default {
mixins: [detail],
components: {
Cell,
Group,
XInput,
PopupPicker,
XTextarea,
Toast,
FlowDetail,
TaskConfirm
},
filters: {
numberComma
},
data() {
return {
showConfirm: false, // 是否展示原因弹窗
result: 0,
transCode: this.$route.query.transCode,
canSubmit: this.$route.query.canSubmit,
pageSwiper: null,
listObj: [
{
title: "请填写项目名称",
items: [
{
title: "项目名称",
key: "projectName",
value: ""
}
]
},
{
title: "请填写市场宣传",
items: [
{
title: "市场宣传",
key: "productMarketing",
value: ""
}
]
},
{
title: "请选择您申请的类型",
items: [
{
title: "宣品类型",
key: "publicityType",
value: ""
}
]
},
{
title: "请填写明细",
items: [
{
title: "单价",
key: "agoraPrice",
value: ""
},
{
title: "数量",
key: "agoraNumber",
value: ""
},
{
title: "合计",
key: "total",
value: ""
}
]
},
{
title: "费用所属",
items: [
{
title: "费用所属事业部",
key: "agoraCostBU",
value: ""
},
{
title: "费用所属部门",
key: "agoraCostDepartment",
value: ""
},
{
title: "核算归属省份",
key: "agoraCheckProvince",
value: ""
},
{
title: "费用所属银行",
key: "agoraCostBank",
value: ""
}
]
},
{
title: "要说点什么吗？",
items: [
{
title: "说明",
key: "comment",
value: ""
}
]
}
],
listData: []
};
},
methods: {

endToast(taskId, data) {
createService
.examineTask(taskId, data)
.then(res => {
if (res.success) {
this.$vux.toast.show({
text: res.message,
position: "middle",
type: "text",
onShow() {
setTimeout(() => {
this.$vux.toast.hide();
this.$router.go(-1);
}, 800);
}
});
}
})
.catch(c => {
this.$vux.toast.show({
text: c.message,
position: "middle",
type: "text",
onShow() {
setTimeout(() => {
this.$vux.toast.hide();
}, 800);
}
});
});
},

end(num) {
this.showConfirm = true;

if (num == 0) {
this.result = 0;
} else if (num == 1) {

this.result = 1;
}
},

confirm(reason) {
let taskId = this.$route.query.taskId,
transCode = this.$route.query.transCode,
data = { result: this.result, transCode: transCode, comment: reason };
if (this.result === 0 && !reason) {
this.$vux.toast.show({
text: "拒绝理由不能为空",
type: "text",
position: "middle"
});
return;
} else {
this.endToast(taskId, data);
}
}
},
created() {
this.$nextTick(() => {
this.pageSwiper = new Swiper(".swiper-container", {});
});
}
};



