
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'
import Toast from 'vux/src/components/toast/index.vue'

import createService from "../../service/createService";
import { setTimeout } from "timers";
import Swiper from "swiper";
import FlowDetail from "./../components/FlowDetail";
import TaskConfirm from "./../components/TaskConfirm";
import detail from "./../mixins/detail";
export default {
components: {
Cell,
Group,
Toast,
FlowDetail,
TaskConfirm
},
filters: {
numberComma
},
data() {
return {
showConfirm: false, // 是否展示原因弹窗
result: 0,
transCode: this.$route.query.transCode,
canSubmit: this.$route.query.canSubmit,
pageSwiper: null,
listObj: [
{
title: "请选择资产类型",
items: [
{
title: "资产类型",
key: "assetType",
value: ""
}
]
},
{
title: "请填写资产型号/规格",
items: [
{
title: "资产型号/规格",
key: "assetModel",
value: ""
}
]
},
{
title: "请选择计量单位",
items: [
{
title: "计量单位",
key: "meteringUnit",
value: ""
}
]
},
{
title: "请填写明细",
items: [
{
title: "单价",
key: "assetPrice",
value: ""
},
{
title: "数量",
key: "assetNumber",
value: ""
},
{
title: "合计",
key: "assetCostTotal",
value: ""
}
]
},
{
title: "请填写部门",
items: [
{
title: "申请部门",
key: "applyDepartment",
value: ""
},
{
title: "使用部门",
key: "useDepartment",
value: ""
}
]
},
{
title: "费用所属",
items: [
{
title: "费用所属事业部",
key: "assetCostBU",
value: ""
},
{
title: "费用所属部门",
key: "assetCostDepartment",
value: ""
},
{
title: "核算归属省份",
key: "assetCheckProvince",
value: ""
},
{
title: "费用所属银行",
key: "assetCostBank",
value: ""
}
]
},
{
title: "要说点什么吗？",
items: [
{
title: "说明",
key: "comment",
value: ""
}
]
}
],
listData: []
};
},
methods: {

endToast(taskId, data) {
createService
.examineTask(taskId, data)
.then(res => {
if (res.success) {
this.$vux.toast.show({
text: res.message,
position: "middle",
type: "text",
onShow() {
setTimeout(() => {
this.$vux.toast.hide();
this.$router.go(-1);
}, 800);
}
});
}
})
.catch(c => {
this.$vux.toast.show({
text: c.message,
position: "middle",
type: "text",
onShow() {
setTimeout(() => {
this.$vux.toast.hide();
}, 800);
}
});
});
},

end(num) {
this.showConfirm = true;

if (num == 0) {
this.result = 0;
} else if (num == 1) {

this.result = 1;
}
},

confirm(reason) {
let taskId = this.$route.query.taskId,
transCode = this.$route.query.transCode,
data = { result: this.result, transCode: transCode, comment: reason };
if (this.result === 0 && !reason) {
this.$vux.toast.show({
text: "拒绝理由不能为空",
type: "text",
position: "middle"
});
return;
} else {
this.endToast(taskId, data);
}
}
},
created() {
this.$nextTick(() => {
this.pageSwiper = new Swiper(".swiper-container", {});
});
},
mounted() {},
computed: {
totalCost() {
let { assetPrice = 0, assetNumber = 0 } = this.formData;
return `￥${numberComma(Number(assetPrice) * Number(assetNumber))}`;
}
},
mixins: [detail]
};



