
import detail from './../mixins/detail'
import numberComma from 'vux/src/tools/number/comma.js'


export default {
name: "mettingDetail",
mixins: [detail],
data() {
return {
listData: [
{
title: '请填写会议安排明细',
items: [
{
title: '开始日期',
key: 'begin',
value: '',
}, {
title: '结束日期',
key: 'end',
value: '',
}, {
title: '省份',
key: 'province',
value: '',
}, {
title: '城市',
key: 'city',
value: '',
}, {
title: '酒店名称',
key: 'hotelName',
value: '',
}, {
title: '房间均价',
key: 'roomAveragePrice',
value: '',
}, {
title: '房间数量',
key: 'roomNumber',
value: '',
}, {
title: '总人数',
key: 'headCount',
value: '',
}, {
title: '场地费用',
key: 'siteFees',
value: '',
}, {
title: '路费合计',
key: 'wayFees',
value: '',
}, {
title: '餐饮合计',
key: 'repastFees',
value: '',
}
]
}, {
title: '请填写会议相关事宜',
items: [
{
title: '人员范围',
key: 'personScope',
value: '',
isTextArea: true
}, {
title: '会议议程',
key: 'agenda',
value: '',
isTextArea: true
}
]
}, {
title: '费用归属',
items: [
{
title: '费用所属事业部',
key: 'costBU',
value: '',
}, {
title: '费用所属部门',
key: 'costDepartment',
value: '',
}, {
title: '核算归属省份',
key: 'checkProvince',
value: '',
}, {
title: '费用所属银行',
key: 'costBank',
value: '',
}
]
}
],
}
},
computed: {
totalCost() {
let {roomNumber = 0, roomAveragePrice = 0, siteFees = 0, wayFees = 0, repastFees = 0} = this.formData;
return `￥${numberComma(Number(roomNumber) * Number(roomAveragePrice) + Number(siteFees) + Number(wayFees) + Number(repastFees))}`;
},
}
}



