
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'

import createService from './../../service/createService'
import CascadePickers from './../components/CascadePickers'
import common from './../mixins/common'

export default {
components: {
Cell,
Group,
XInput,
Datetime,
PopupPicker,
CascadePickers,
},
data() {
return {
listid: '',
h_type: [['省仓', '办事处']],
type_value: [],
h_hdtype: [['新增', '搬家']],
hd_value: [],
bj_list: [
{
title: '新增/搬家原因',
key: 'moveReason',
type: 'text',
}, {
title: '入驻人数',
key: 'checkInNumber',
type: 'number',
}, {
title: '房屋面积',
key: 'area',
type: 'text',
}, {
title: '月租',
key: 'rental',
type: 'number',
}, {
title: '付款方式',
key: 'paymentType',
type: 'text',
}, {
title: '租期',
key: 'tenancy',
type: 'number',
}
],
formData: {
'handlerName': '', // 经办人
'handlerAreaName': '', // 所属区域
'handlerUnitName': '', // 经办部门
'handlerRoleName': '', // 经办角色
'creatorName': '', // 创建者
'crtTime': '', // 创建时间
'modifer': '', // 修改者
'modTime': '', // 修改时间
'handerId': '', // 经办人id
'transType': '房屋立项申请', // 交易类型
'handlerUnitId': '', // 经办部门id
'handlerRoleId': '', // 经办角色id
'cjz': '', // 创建者id
'xgz': '', // 修改者id
'handlerArea': '', // 所属区域id
'office': '', // 省仓/办事处
'moveType': '', // 异动类型
'area': '', // 面积 (㎡)
'checkInNumber': '', // 入驻人数
'paymentType': '', // 付款方式 (月)
'rental': '', // 月租
'tenancy': '', // 租期 (月)
'houseCostTotal': '', // 费用合计（房屋立项）
'begin': '', // 始于
'end': '', // 止于
'moveReason': '', // 新增/搬家原因
'costBU': '',// 费用所属事业部
'costDepartment': '',// 费用所属部门
'checkProvince': '',// 核算归属省份
'costBank': ''// 费用所属银行
},
hasDefault: false,
cascadeValue: {},
sessionKey: '',
formKey: '',
transCode: '',
showPage: true,
taskId: '',
}
},
mixins: [common],
computed: {
totalCost() {
let {tenancy = 0, rental = 0} = this.formData;
return `￥${numberComma(Number(tenancy) * Number(rental))}`;
}
},
methods: {
goMylist() { //我的提交
this.$router.push({
path: '/myList',
query: {
listId: this.listid
}
})
},
goflow() {
let lists = [
{
title: '地点类型',
key: 'office',
value: '',
}, {
title: '处理类型',
key: 'moveType',
value: '',
}, {
title: '新增/搬家原因',
key: 'moveReason',
value: '',
}, {
title: '入驻人数',
key: 'checkInNumber',
value: '',
}, {
title: '房屋面积',
key: 'area',
value: '',
}, {
title: '月租',
key: 'rental',
value: '',
}, {
title: '付款方式',
key: 'paymentType',
value: '',
}, {
title: '租期',
key: 'tenancy',
value: '',
}, {
title: '租期开始时间',
key: 'begin',
value: '',
}, {
title: '租期结束时间',
key: 'end',
value: '',
}, {
title: '费用所属事业部',
key: 'costBU',
value: '',
}, {
title: '费用所属部门',
key: 'costDepartment',
value: '',
}, {
title: '核算归属省份',
key: 'checkProvince',
value: '',
}, {
title: '费用所属银行',
key: 'costBank',
value: '',
}
];
let warn = '';
this.formData.houseCostTotal = this.totalCost.replace(/￥/g, '').replace(/,/g, '');
Object.assign(this.formData, this.$refs.cascadePickers.getFormData());
lists.every(item => {
if (!this.formData[item.key]) {
warn = `${item.title}不能为空`;
return false
}
return true
});
if (warn) {
this.showToast(warn);
return
}
sessionStorage.setItem(this.sessionKey, JSON.stringify(this.formData));
this.$router.push({
path: '/flow',
query: {
list: this.listid,
taskId: this.taskId,
}
})
},

officeChange(val) {
this.formData.office = val[0] || '';
},

moveTypeChange(val) {
this.formData.moveType = val[0] || '';
},

getFormData() {
return createService.getFormData({
formKey: this.formKey,
transCode: this.transCode,
}).then(data => {
this.hasDefault = true;
this.showPage = true;
let {formData = {}, success = true, message = ''} = data;

if (!success) {
this.showToast(message);
return;
}

formData.begin = this.changeDate(formData.begin);
formData.end = this.changeDate(formData.end);



this.type_value = [formData.office];
this.hd_value = [formData.moveType];

this.cascadeValue = {
costBU: formData.costBU,
costDepartment: formData.costDepartment,
checkProvince: formData.checkProvince,
costBank: formData.costBank
};

this.formData = formData;
this.$nextTick(() => {

this.hasDefault = false;
})
}).catch(e => {
this.showToast(e.message);
});
},
},
beforeRouteLeave(to, from, next) {
let {name} = to;
if (name === 'Home') {

sessionStorage.removeItem(this.sessionKey)
}
next();
},
created() {
let {query} = this.$route;
let requestPromises = [];
this.listid = query.list;
this.formKey = query.formKey;
this.transCode = query.transCode;
this.taskId = query.taskId;
this.sessionKey = `${this.listid}-FORMDATA`;
this.showLoading = true;
let formData = sessionStorage.getItem(this.sessionKey);
if (formData) {
formData = JSON.parse(formData);

this.formData.begin = formData.begin;
this.formData.end = formData.end;
}


if (!formData && this.formKey) {
this.showPage = false;
requestPromises.push(this.getFormData());
}

this.$nextTick(() => {
requestPromises.push(this.$refs.cascadePickers.init());
Promise.all(requestPromises).then(data => {
this.showLoading = false;
})
})
},
mounted() {
let formData = sessionStorage.getItem(this.sessionKey);
if (formData) {
this.hasDefault = true;
formData = JSON.parse(formData);
this.formData = formData;
this.type_value = [formData.office];
this.hd_value = [formData.moveType];
this.cascadeValue = {
costBU: formData.costBU,
costDepartment: formData.costDepartment,
checkProvince: formData.checkProvince,
costBank: formData.costBank
};
sessionStorage.removeItem(this.sessionKey);
this.$nextTick(() => {

this.hasDefault = false;
})
}
}
}



