import mylistService from './../service/mylistService'
import common from './mixins/common'

export default {
  name: "operation",
  data() {
    return {
      sel_list: [
        {
          name: '查看我的',
          type: 'view',
          count: 0,
        }, {
          name: '发起申请',
          type: 'create',
        }
      ],
    }
  },
  methods: {
    goForward(item) {
      let {type} = item;
      let {view, list, create} = this.$route.query;
      let path = '/';
      let query = {};
      switch (type) {
        case 'create':
          path = `/${create}`;
          query = {
            view: view,
            list: list,
          };
          break;
        default:
          path = '/mylist';
          query = {
            listId: list,
          };
          break;
      }
      this.$router.push({
        path,
        query,
      })
    }
  },
  created() {
    /*mylistService.getTasksListData({
      entityId: 20000,
      _dc: Date.parse(new Date()),
      listID: this.$route.query.list,
      para1: "",
    }).then(data => {
      let {dataCount = 0} = data;
      console.log(data)
    }).catch(e => {
      this.showToast(e.message);
    })*/
  },
  mounted() {
    //清除mylist的选项卡状态
    if(sessionStorage.getItem('MYLIST_TAB')){
        sessionStorage.removeItem('MYLIST_TAB')
    }
  }
}



