
import createService from './../../service/createService'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'


const PARENT_ID = '60d2cb55-8066-4c85-b8ea-60bde9be641b';
export default {
name: "CascadePickers",
props: {
value: {
type: Object,
default() {
return {}
}
},
hasDefault: {
type: Boolean,
default: false
}
},
components: {Group, PopupPicker},
data() {
return {
buDeptList: [],
buSelected: [],
deptList: [],
deptSelected: [],
provList: [],
provSelected: [],
bankList: [],
bankSelected: [],
formData: {
costBU: '',// 费用所属事业部
costDepartment: '',// 费用所属部门
checkProvince: '',// 核算归属省份
costBank: ''
},
}
},
watch: {
value: {
handler(val) {
this.buSelected = [val.costBU];
this.deptSelected = [val.costDepartment];
this.provSelected = [val.checkProvince];
this.bankSelected = [val.costBank];
this.formData = Object.assign({}, val)
},
}
},
methods: {

assembleList(list) {
let tmp = [];
tmp = list && list.map(item => {
let {unitName} = item;
return {
name: unitName,
value: unitName,
}
});
return tmp
},

reqData(listKey, param = {}) {
return createService.getAccountingUnitByid(Object.assign({
parentId: PARENT_ID,
}, param)).then(data => {
let {tableContent = []} = data;
this[listKey] = this.assembleList(tableContent);
}).catch(e => {
this.$parent.showToast(e.message);
})
},

getBuDept() {
return this.reqData('buDeptList');
},

getDept() {
this.deptList = [];
if (!this.hasDefault) {
this.deptSelected = [];
}
let [name1] = this.buSelected;
if (!name1) {
return
}
this.reqData('deptList', {
key: 'N2',
name1,
});
},

getProv() {
this.provList = [];
if (!this.hasDefault) {
this.provSelected = [];
}
let [name1] = this.buSelected;
let [name2] = this.deptSelected;
if (!name1 || !name2) {
return
}
this.reqData('provList', {
key: 'N3',
name1,
name2,
});
},

getBank() {
this.bankList = [];
if (!this.hasDefault) {
this.bankSelected = [];
}
let [name1] = this.buSelected;
let [name2] = this.deptSelected;
let [name3] = this.provSelected;
if (!name1 || !name2 || !name3) {
return
}
this.reqData('bankList', {
key: 'N4',
name1,
name2,
name3,
});
},

buDeptChange(val) {
this.formData.costBU = val[0] || '';
this.getDept();
this.getProv();
this.getBank();
},

deptChange(val) {
this.formData.costDepartment = val[0] || '';
this.getProv();
this.getBank();
},

provChange(val) {
this.formData.checkProvince = val[0] || '';
this.getBank();
},

bankChange(val) {
this.formData.costBank = val[0] || '';
},
getFormData() {
return this.formData
},

init() {
return this.getBuDept();
},
},
created() {
}
}



