
import createService from './../../service/createService'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'

import CascadePickers from './../components/CascadePickers'
import common from './../mixins/common'

export default {
components: {
Cell,
Group,
XInput,
Datetime,
XTextarea,
PopupPicker,
CascadePickers
},
data() {
return {
listid: '',
meetList: [
{
title: '酒店名称',
key: 'hotelName',
type: 'text',
}, {
title: '房间均价',
key: 'roomAveragePrice',
type: 'number',
}, {
title: '房间数量',
key: 'roomNumber',
type: 'number',
}, {
title: '总人数',
key: 'headCount',
type: 'number',
}, {
title: '场地费用',
key: 'siteFees',
type: 'number',
}, {
title: '路费合计',
key: 'wayFees',
type: 'number',
}, {
title: '餐饮合计',
key: 'repastFees',
type: 'number',
}
],
formData: {
'handlerName': '', // 经办人
'handlerAreaName': '', // 所属区域
'handlerUnitName': '', // 经办部门
'handlerRoleName': '', // 经办角色
'creatorName': '', // 创建者
'crtTime': '', // 创建时间
'modifer': '', // 修改者
'modTime': '', // 修改时间
'handerId': '', // 经办人id
'transType': '会务立项申请', // 交易类型
'handlerUnitId': '', // 经办部门id
'handlerRoleId': '', // 经办角色id
'cjz': '', // 创建者id
'xgz': '', // 修改者id
'handlerArea': '', // 所属区域id
'province': '', // 省
'city': '', // 市
'hotelName': '', // 酒店名称
'headCount': '', // 总人数
'roomNumber': '', // 房间数量
'roomAveragePrice': '', // 房间均价
'siteFees': '', // 会议场地费用
'hotelFees': '', // 住宿合计
'wayFees': '', // 路费合计
'repastFees': '', // 餐饮合计
'meetingCostTotal': '', // 费用合计（会务立项）
'personScope': '', // 人员范围
'agenda': '', // 会议议程
'begin': '', // 始于
'end': '', // 止于
'costBU': '',// 费用所属事业部
'costDepartment': '',// 费用所属部门
'checkProvince': '',// 核算归属省份
'costBank': ''// 费用所属银行
},
provinceList: [],
provinceSelected: [],
cityList: [],
citySelected: [],
hasDefault: false,
cascadeValue: {},
sessionKey: '',
formKey: '',
transCode: '',
showPage: true,
taskId: '',
}
},
computed: {

totalCost() {
let {roomNumber = 0, roomAveragePrice = 0, siteFees = 0, wayFees = 0, repastFees = 0} = this.formData;
return `￥${numberComma(Number(roomNumber) * Number(roomAveragePrice) + Number(siteFees) + Number(wayFees) + Number(repastFees))}`;
},

hotelFees() {
let {roomNumber = 0, roomAveragePrice = 0} = this.formData;
return Number(roomNumber) * roomAveragePrice;
},
},
mixins: [common],
methods: {
goMylist() { //我的提交
this.$router.push({
path: '/myList',
query: {
listId: this.listid
}
})
},
goflow() {
let lists = [
{
title: '开始日期',
key: 'begin',
}, {
title: '结束日期',
key: 'end',
}, {
title: '省份',
key: 'province',
}, {
title: '城市',
key: 'city',
}, {
title: '酒店名称',
key: 'hotelName',
}, {
title: '房间均价',
key: 'roomAveragePrice',
}, {
title: '房间数量',
key: 'roomNumber',
}, {
title: '总人数',
key: 'headCount',
}, {
title: '场地费用',
key: 'siteFees',
}, {
title: '路费合计',
key: 'wayFees',
}, {
title: '餐饮合计',
key: 'repastFees',
}, {
title: '人员范围',
key: 'personScope',
value: '',
isTextArea: true
}, {
title: '会议议程',
key: 'agenda',
value: '',
isTextArea: true
}, {
title: '费用所属事业部',
key: 'costBU',
}, {
title: '费用所属部门',
key: 'costDepartment',
}, {
title: '核算归属省份',
key: 'checkProvince',
}, {
title: '费用所属银行',
key: 'costBank',
}
];
let warn = '';
this.formData.meetingCostTotal = this.totalCost.replace(/￥/g, '').replace(/,/g, '');
this.formData.hotelFees = this.hotelFees;
Object.assign(this.formData, this.$refs.cascadePickers.getFormData());
lists.every(item => {
if (!this.formData[item.key]) {
warn = `${item.title}不能为空`;
return false
}
return true
});
if (warn) {
this.showToast(warn);
return
}
sessionStorage.setItem(this.sessionKey, JSON.stringify(this.formData));
this.$router.push({
path: '/flow',
query: {
list: this.listid,
taskId: this.taskId,
}
})
},

getProvinceList() {
return createService.getAccountingUnitByid().then(data => {
let {tableContent = []} = data;
this.provinceList = tableContent && tableContent.map(item => {
let {unitName} = item;
return {
name: unitName,
value: unitName,
}
});
}).catch(e => {
this.showToast(e.message);
});
},

getCityList() {
this.cityList = [];
if (!this.hasDefault) {
this.citySelected = [];
}
if (!this.formData.province) {
return
}
createService.getAccountingUnitByid({
key: 'N2',
name1: this.formData.province
}).then(data => {
let {tableContent = []} = data;
this.cityList = tableContent && tableContent.map(item => {
let {unitName} = item;
return {
name: unitName,
value: unitName,
}
});
}).catch(e => {
this.showToast(e.message);
});
},

provinceChange(val) {
this.formData.province = val[0] || '';
this.getCityList();
},

cityChange(val) {
this.formData.city = val[0] || '';
},

getFormData() {
return createService.getFormData({
formKey: this.formKey,
transCode: this.transCode,
}).then(data => {
this.hasDefault = true;
this.showPage = true;
let {formData = {}, success = true, message = ''} = data;

if (!success) {
this.showToast(message);
return;
}

formData.begin = this.changeDate(formData.begin);
formData.end = this.changeDate(formData.end);



this.provinceSelected = [formData.province];
this.citySelected = [formData.city];
this.cascadeValue = {
costBU: formData.costBU,
costDepartment: formData.costDepartment,
checkProvince: formData.checkProvince,
costBank: formData.costBank
};

this.formData = formData;
this.$nextTick(() => {

this.hasDefault = false;
})
}).catch(e => {
this.showToast(e.message);
});
},
},
beforeRouteLeave(to, from, next) {
let {name} = to;
if (name === 'Home') {

sessionStorage.removeItem(this.sessionKey)
}
next();
},
created() {
let {query} = this.$route;
let requestPromises = [];
this.listid = query.list;
this.formKey = query.formKey;
this.transCode = query.transCode;
this.taskId = query.taskId;
this.sessionKey = `${this.listid}-FORMDATA`;
this.showLoading = true;
let formData = sessionStorage.getItem(this.sessionKey);
if (formData) {
formData = JSON.parse(formData);

this.formData.begin = formData.begin;
this.formData.end = formData.end;
}


if (!formData && this.formKey) {
this.showPage = false;
requestPromises.push(this.getFormData());
}

this.$nextTick(() => {
requestPromises.push(this.getProvinceList());
requestPromises.push(this.$refs.cascadePickers.init());
Promise.all(requestPromises).then(data => {
this.showLoading = false;
})
})
},
mounted() {
let formData = sessionStorage.getItem(this.sessionKey);
if (formData) {
this.hasDefault = true;
formData = JSON.parse(formData);
this.formData = Object.assign({}, this.formData, formData);
this.provinceSelected = [formData.province];
this.citySelected = [formData.city];
this.cascadeValue = {
costBU: formData.costBU,
costDepartment: formData.costDepartment,
checkProvince: formData.checkProvince,
costBank: formData.costBank
};
sessionStorage.removeItem(this.sessionKey);
this.$nextTick(() => {

this.hasDefault = false;
})
}
}
}



