import detail from './../mixins/detail'

export default {
  mixins: [detail],
  data() {
    return {
      listData: [
        {
          title: '请选择地点类型',
          items: [
            {
              title: '地点类型',
              key: 'office',
              value: '',
            }, {
              title: '处理类型',
              key: 'moveType',
              value: '',
            }
          ]
        }, {
          title: '请填写明细',
          items: [
            {
              title: '新增/搬家原因',
              key: 'moveReason',
              value: '',
            }, {
              title: '入驻人数',
              key: 'checkInNumber',
              value: '',
            }, {
              title: '房屋面积',
              key: 'area',
              value: '',
            }, {
              title: '月租',
              key: 'rental',
              value: '',
            }, {
              title: '付款方式',
              key: 'paymentType',
              value: '',
            }, {
              title: '租期',
              key: 'tenancy',
              value: '',
            }, {
              title: '租期开始时间',
              key: 'begin',
              value: '',
            }, {
              title: '租期结束时间',
              key: 'end',
              value: '',
            }
          ]
        }, {
          title: '费用归属',
          items: [
            {
              title: '费用所属事业部',
              key: 'costBU',
              value: '',
            }, {
              title: '费用所属部门',
              key: 'costDepartment',
              value: '',
            }, {
              title: '核算归属省份',
              key: 'checkProvince',
              value: '',
            }, {
              title: '费用所属银行',
              key: 'costBank',
              value: '',
            }
          ]
        }
      ],
    }
  },
  computed: {
    totalCost() {
      let {tenancy = 0, rental = 0} = this.formData;
      return `￥${numberComma(Number(tenancy) * Number(rental))}`;
    }
  }
}



