
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'

import spreadService from "../../service/spreadService";
import createService from "../../service/createService";
export default {
components: {
Cell,
Group,
XInput,
PopupPicker,
XTextarea
},
filters: {
numberComma
},
data() {
return {
baseInfo: "",
formData: "",
xp_list: [
{
name: "", //项目名称
conduct: "", //市场宣传
type: [
[
"单页",
"三折页",
"X展架",
"易拉宝",
"三角牌",
"海报",
"吊旗",
"地贴",
"道具"
]
], //宣品类型
s_type: [], //选择类型
unitprice: "", //单价
num: "", //数量
bulist: [], //费用所属事业部
buOn: [], //费用所属事业部选中
deptlist: [], //费用所属部门
deptOn: [], //费用所属部门选中
provlist: [], //核算归属省份
provOn: [], //核算归属省份选中
banklist: [], //费用所属银行
bankOn: [], //费用所属银行选中
explain: "", //说明
status: false
}
], // 宣品 填写内容
searchInfo: false
};
},
methods: {

getSelect(data, N, name1, name2, name3, num, index, e) {
if (e === true) {
if (num == "1") {
this.xp_list[index].deptOn = [];
this.xp_list[index].provOn = [];
this.xp_list[index].bankOn = [];
} else if (num == "2") {
this.xp_list[index].provOn = [];
this.xp_list[index].bankOn = [];
} else if (num == "3") {
this.xp_list[index].bankOn = [];
}
} else {
return;
}
data.length = 0;
let jsonData = {
_dc: Date.parse(new Date()),
key: N,
parentId: "60d2cb55-8066-4c85-b8ea-60bde9be641b",
name1: name1,
name2: name2,
name3: name3,
page: 1,
start: 0,
limit: 10000
},
arr = [];
spreadService
.getAccounting(jsonData)
.then(res => {
for (let i = 0; i < res.tableContent.length; i++) {
arr.push(res.tableContent[i].unitName);
}
data.push(arr);
})
.catch(c => {
console.log(c);
});
},
changeType(idx) {
this.xp_list[idx].status = true;
},
plusType() {

this.xp_list.push({
name: "", //项目名称
conduct: "", //市场宣传
type: [
[
"单页",
"三折页",
"X展架",
"易拉宝",
"三角牌",
"海报",
"吊旗",
"地贴",
"道具"
]
], //宣品类型
s_type: [], //选择类型
unitprice: "", //单价
num: "", //数量
bulist: [], //费用所属事业部
buOn: [], //费用所属事业部选中
deptlist: [], //费用所属部门
deptOn: [], //费用所属部门选中
provlist: [], //核算归属省份
provOn: [], //核算归属省份选中
banklist: [], //费用所属银行
bankOn: [], //费用所属银行选中
explain: "", //说明
status: false
});
this.getSelect(
this.xp_list[this.xp_list.length - 1].bulist,
"N1",
111,
111,
111,
0,
this.xp_list.length - 1,
true
);
},

delateOne() {
this.xp_list.pop();
},
goflow() {
let jsonData = {
handlerName: this.baseInfo.nickname, //经办人
handlerAreaName: this.baseInfo.area, //所属区域
handlerUnitName: this.baseInfo.groupName, //经办部门
handlerRoleName: this.baseInfo.position, //经办角色
creatorName: this.baseInfo.nickname, //创建者
crtTime: Date.parse(new Date()), //创建时间
modifer: "", //修改者
modTime: "", //修改时间
handerId: this.baseInfo.userId, //经办人id
transType: "市场宣传", //交易类型
handlerUnitId: this.baseInfo.groupNameID, //经办部门id
handlerRoleId: this.baseInfo.roleID, //经办角色id
cjz: this.baseInfo.userId, //创建者id
xgz: "", //修改者id
handlerArea: this.baseInfo.areaID, //所属区域id
order: {
dataSet: []
}
};
if (this.formData != "") {
jsonData.biId = this.formData.biId;
jsonData.biReferenceId = this.formData.biReferenceId;
jsonData.transType = this.formData.transType;
jsonData.modTime = this.formData.modTime;
jsonData.modifer = this.formData.modifer;
jsonData.xgz = this.formData.xgz;
}
for (let i = 0; i < this.xp_list.length; i++) {
let item = this.xp_list[i];
if (item.name == "") {
this.layer("请填写项目名称", "cancel");
return;
} else if (item.conduct == "") {
this.layer("请填写市场宣传", "cancel");
return;
} else if (item.s_type.length == 0) {
this.layer("请选择宣品类型", "cancel");
return;
} else if (item.unitprice == "") {
this.layer("请填写单价", "cancel");
return;
} else if (item.num == "") {
this.layer("请填写数量", "cancel");
return;
} else if (item.buOn.length == 0) {
this.layer("请选择事业部", "cancel");
return;
} else if (item.deptOn.length == 0) {
this.layer("请选择部门", "cancel");
return;
} else if (item.provOn.length == 0) {
this.layer("请选择省份", "cancel");
return;
} else if (item.bankOn.length == 0) {
this.layer("请选择银行", "cancel");
return;
}
jsonData.order.dataSet.push({
projectName: item.name, //项目名称
productMarketing: item.conduct, //市场宣传
publicityType: item.s_type[0], //宣品类型
agoraNumber: item.num, //数量
agoraPrice: item.unitprice, //单价
total: Number(item.num) * Number(item.unitprice), //总计
agoraCostBU: item.buOn[0], //费用所属事业部
agoraCostDepartment: item.deptOn[0], //费用所属部门
agoraCheckProvince: item.provOn[0], //核算归属省份
agoraCostBank: item.bankOn[0], //费用所属银行
comment: item.explain, //说明
fgCode: "fgwmiw" 
});
if (this.formData != "") {
if (this.formData.order.dataSet[i]) {
jsonData.order.dataSet[i].uncalcID = this.formData.order.dataSet[
i
].uncalcID;
} else {
jsonData.order.dataSet[i].uncalcID = "";
}
}
}
sessionStorage.setItem(
this.$route.query.list + "-FORMDATA",
JSON.stringify(jsonData)
);
let queryData = {};
if (this.$route.query.taskId) {
queryData = {
list: this.$route.query.list,
taskId: this.$route.query.taskId
};
} else {
queryData = { list: this.$route.query.list };
}
this.$router.push({ path: "/flow", query: queryData });
},
layer(info, type) {
this.$vux.toast.show({
text: info,
type: type,
position: "middle",
time: "900"
});
},
cacheData(dataSet) {
let sessionArr = [];
for (let i = 0; i < dataSet.length; i++) {
sessionArr.push({
name: dataSet[i].projectName, //项目名称
conduct: dataSet[i].productMarketing, //市场宣传
type: [
[
"单页",
"三折页",
"X展架",
"易拉宝",
"三角牌",
"海报",
"吊旗",
"地贴",
"道具"
]
], //宣品类型
s_type: [dataSet[i].publicityType], //选择类型
unitprice: dataSet[i].agoraPrice, //单价
num: dataSet[i].agoraNumber, //数量
bulist: [], //费用所属事业部
buOn: [dataSet[i].agoraCostBU], //费用所属事业部选中
deptlist: [], //费用所属部门
deptOn: [dataSet[i].agoraCostDepartment], //费用所属部门选中
provlist: [], //核算归属省份
provOn: [dataSet[i].agoraCheckProvince], //核算归属省份选中
banklist: [], //费用所属银行
bankOn: [dataSet[i].agoraCostBank], //费用所属银行选中
explain: dataSet[i].comment, //说明
status: true
});
}
this.xp_list = sessionArr;
},
listDefault() {
for (let j = 0; j < this.xp_list.length; j++) {
let xp_item = this.xp_list[j];
this.getSelect(xp_item.bulist, "N1", 111, 111, 111, 0, j, true);
if (xp_item.deptOn.length != 0) {
this.getSelect(
xp_item.deptlist,
"N2",
xp_item.buOn[0],
111,
111,
0,
j,
true
);
}
if (xp_item.provOn.length != 0) {
this.getSelect(
xp_item.provlist,
"N3",
xp_item.buOn[0],
xp_item.deptOn[0],
111,
0,
j,
true
);
}
if (xp_item.bankOn.length != 0) {
this.getSelect(
xp_item.banklist,
"N4",
xp_item.buOn[0],
xp_item.deptOn[0],
xp_item.provOn[0],
0,
j,
true
);
}
}
}
},
created() {},
mounted() {

spreadService
.getBaseInfo()
.then(res => {
this.baseInfo = res;
})
.catch(c => {
console.log(c);
});

if (this.$route.query.formKey && this.$route.query.transCode) {
createService
.getFormData({
formKey: this.$route.query.formKey,
transCode: this.$route.query.transCode
})
.then(res => {
this.formData = res.formData;
this.cacheData(res.formData.order.dataSet);
this.listDefault();
})
.catch(c => {
console.log(c);
});
} else if (sessionStorage.getItem(this.$route.query.list + "-FORMDATA")) {
let dataSet = JSON.parse(
sessionStorage.getItem(this.$route.query.list + "-FORMDATA")
).order.dataSet;
this.cacheData(dataSet);
this.listDefault();
} else {
this.listDefault();
}
},
computed: {

total() {
let total = 0;
for (let i = 0; i < this.xp_list.length; i++) {
total +=
Number(this.xp_list[i].unitprice) * Number(this.xp_list[i].num);
}
return total;
}
}
};



