
import Flow from 'vux/src/components/flow/flow.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Panel from 'vux/src/components/panel/index.vue'

import createService from './../../service/createService'

export default {
props: {
transCode: {
type: String,
default: ''
}
},
name: "FlowDetail",
data() {
return {

governor: {
stateDone: true,
lineDone: false,
tip: '',
},

committee: {
stateDone: false,
lineDone: false,
tip: '',
},

vicePresident: {
stateDone: false,
lineDone: false,
tip: '',
},

finance: {
stateDone: false,
lineDone: false,
tip: '',
},

ceo: {
stateDone: false,
lineDone: false,
tip: '',
},
commentList: [],
}
},
components: {
Flow,
Cell,
Group,
FlowLine,
FlowState,
LoadMore,
Panel,
},
methods: {

getFlows() {
return createService.getWorkFlow({
transCode: this.transCode
}).then(data => {
let last = data.pop();
let showList = ['常委审批', '副总裁审批', '财务审批', '总裁审批'];
data && data.forEach((item, index) => {

if (showList.indexOf(item.actName) !== -1) {
this.commentList.push({
title: item.name,
desc: item.comment || '无',
time: item.startTime,
meta: {
date: item.startTime
}
})
}
});
this.assembleFlow(last);
}).catch(e => {
this.showToast(e.message);
})
},

assembleFlow(item){
let done = {
stateDone: true,
lineDone: true,
tip: '',
};
let doing = {
stateDone: true,
lineDone: false,
tip: '进行中',
};
switch (item.actName) {
case 'Start':
this.governor = doing;
break;
case '常委审批':
this.governor = done;
this.committee = doing;
break;
case '副总裁审批':
this.governor = done;
this.committee = done;
this.vicePresident = doing;
break;
case '财务审批':
this.governor = done;
this.committee = done;
this.vicePresident = done;
this.finance = doing;
break;
case '总裁审批':
this.governor = done;
this.committee = done;
this.vicePresident = done;
this.finance = done;
this.ceo = {
stateDone: false,
lineDone: false,
tip: '',
};
break;
case '重新提交':
this.governor = doing;
break;
case '生效表单':
break;
case 'End':
this.governor = done;
this.committee = done;
this.vicePresident = done;
this.finance = done;
this.ceo = done;
break;
default:
break;
}
},
showToast(text = '') {
this.$vux.toast.show({
text: text,
type: 'text',
position: 'middle',
width: 'auto',
isShowMask: true,
});
},
},
created() {
this.getFlows();
}
}



