
import Flow from 'vux/src/components/flow/flow.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'

import createService from './../../service/createService'
import common from './../mixins/common'

const USER_INFO = 'RFD_CURRENT_USER_INFO';
export default {
components: {
Flow,
Cell,
Group,
XInput,
FlowLine,
FlowState,
},
data() {
return {
showLoading: false,
level_list: {
committee: {
name: '常委',
searchType: 'committee',
value: '',
userId: '',
},
vicePresident: {
name: '副总',
searchType: 'vicePresident',
value: '',
userId: '',
}
},
searchList: [], // 搜索结果列表
searchType: '', // 当前聚焦的输入框类型
hasSelected: false, // 是否选中搜索项
formData: {}, // 表单数据
procCode: '', // 表单编码
searchListTop: 0, // 匹配列表距离顶部的距离
sessionKey: '', // 存储formData的key
taskId: '', // 任务id，修改时会有
};
},
mixins: [common],
methods: {

getProcess() {
return createService.getProcess({
listId: this.listid
}).then(data => {
this.procCode = data[0] && data[0].procCode || '';
}).catch(e => {
this.showToast(e.message);
});
},

getUserInfo() {
return createService.getUser().then(data => {
this.userInfo = data;
return data;
});
},

searchFocus(item, key) {
this.hasSelected = false;
this.searchList = [];
this.searchType = item.searchType;

if (key === 'committee') {
this.searchListTop = '81px';
} else if (key === 'vicePresident') {
this.searchListTop = '126px';
}
},

searchBlur(item) {

if (this.hasSelected) {
return
}

let [matchObj = {}] = this.searchList.filter(sItem => {
return sItem.name === item.value
});

if (matchObj.userId) {
item.userId = matchObj.userId;
item.value = matchObj.name;
this.hasSelected = true;
}
},

getSearch(item) {
let {value} = item;
let params = {
filter: JSON.stringify([{
operator: 'like',
value: value,
property: 'nickname'
}])
};

if (!value || this.hasSelected) {
return
}
item.userId = '';

if (this.searchType === 'vicePresident') {
params.name = '副总裁'
}
createService.getChangWei(params).then(data => {
let {tableContent = []} = data;
this.searchList = tableContent && tableContent.map(item => {
return {
userId: item.userId,
name: item.nickname,
}
})
})
},

searchItemClick(item) {
this.hasSelected = true;
let {name, userId} = item;
this.level_list[this.searchType].value = name;
this.level_list[this.searchType].userId = userId;
},

submitData() {
let warn = '';
let submitData = {};
let wfPara = {};
let submitMethod = 'saveAndStartWf';

Object.values(this.level_list).every(item => {
if (!item.userId) {
warn = `${item.name}不能为空`;
return false
}
return true
});
if (warn) {
this.showToast(warn);
return
}
wfPara = {
'PROC_1806_0005': {
'businessKey': 'KFSCPCGRK',
'createdBy': '',
'常委ID': this.level_list.committee.userId,
'副总裁ID': this.level_list.vicePresident.userId,


}
};

if (this.taskId) {
wfPara.taskId = this.taskId;
wfPara.result = 1;
submitMethod = 'saveAndCommitTask';
submitData.biReferenceId = this.formData.biReferenceId;
}
Object.assign(submitData, {
listId: this.listid,
biComment: '',
formData: JSON.stringify(this.formData),
wfPara: JSON.stringify(wfPara),
});
this.showLoading = true;
createService[submitMethod](submitData).then(data => {
this.showLoading = false;
let {message, success} = data;
if (success && message.indexOf('null') === -1) {
this.showToast('提交成功');
sessionStorage.removeItem(this.sessionKey);
setTimeout(() => {
this.$router.go(-2);
}, 1200)
} else {
this.showToast('提交失败');
}
}).catch(e => {
this.showToast(e.message);
})
},

getBaseInfo() {
let handleBaseInfo = (data = {}) => {
let {nickname, userId, area, areaID, groupName, groupNameID, position, roleID} = data;
area = area.split(',')[0];
areaID = areaID.split(',')[0];
groupName = groupName.split(',')[0];
groupNameID = groupNameID.split(',')[0];
position = position.split(',')[0];
roleID = roleID.split(',')[0];
this.formData.handlerName = nickname;
this.formData.creatorName = nickname;
this.formData.modifer = nickname;
this.formData.handerId = userId;
this.formData.cjz = userId;
this.formData.handlerAreaName = area;
this.formData.handlerArea = areaID;
this.formData.handlerUnitName = groupName;
this.formData.handlerUnitId = groupNameID;
this.formData.handlerRoleName = position;
this.formData.handlerRoleId = roleID;
};

let currentUser = sessionStorage.getItem(USER_INFO);
if (currentUser) {
currentUser = JSON.parse(currentUser);
handleBaseInfo(currentUser);
return Promise.resolve();
}

return createService.getBaseInfoData().then(handleBaseInfo);
},
},
created() {
let {query} = this.$route;
this.listid = query.list;
this.taskId = query.taskId;
this.sessionKey = `${this.listid}-FORMDATA`;
let now = this.changeDate(new Date(), true);
let formData = JSON.parse(sessionStorage.getItem(this.sessionKey) || "{}");
this.formData = Object.assign({}, formData);

this.formData.crtTime = formData.crtTime ? this.changeDate(formData.crtTime, true) : now;
this.formData.modTime = now;

this.showLoading = true;
this.getBaseInfo().then(data => {
this.showLoading = false;
})
}
};



